/**
 * Manages the left to make sure that weird zoomed out page effect doesn't happen
 */
function manageTooltipWidth() {
    const toolTip = document.querySelector('#web3-wallet-address');
    if (!toolTip) {
        console.error(`tooltips missing`);
        return;
    }
    if (isUserConnected()) {
        console.log('setting left to : 0');
        showAddress.style.setProperty("--custom-tooltip-left", "0px");
        return;
    }
    console.log('setting left to : negative');
    showAddress.style.setProperty("--custom-tooltip-left", "-100px");
}


async function login(provider) {
    // const BscChainId = "0x38"; //Bsc Mainnet ID
    const BscChainId = "0x61"; //Bsc Testnet ID


    let user = Moralis.User.current();

    if (provider === 'walletconnect') {
        await Moralis.enableWeb3({ provider: "walletconnect" });
    } else {
        await Moralis.enableWeb3();
    }
    if (!user) {
        console.log(`Provider :${provider} -ln-108`);
        if (provider === 'walletconnect') {
            try {
                console.info(`Authenticating with WalletConnect3`);
                user = await Moralis.authenticate({ provider: "walletconnect" });
                console.info(`Enabling Web3 - ${provider} ln-128`)
                // await Moralis.enableWeb3({ provider: "walletconnect" });
            } catch (error) {
                console.error(`Authentication Failed - Walletconnect - ln-118`);
                console.error(error);
            }
        } else {
            try {
                console.info(`Authenticating with MetaMask ln-123`);
                user = await Moralis.authenticate();
                console.info(`Enabling Web3 - Metamask ln-125`)
                // await Moralis.enableWeb3();
                currentChainIdHex = await Moralis.switchNetwork(BscChainId);
            } catch (error) {
                console.error(`Authentication Failed - MetaMask`);
                console.error(error);
            }
        }
    }

    currentChainIdHex = await Moralis.getChainId();

    if (isUserConnected()) {
        atLogin();
        showAddress.dataset.tip = user.get("ethAddress");
        showAddress.style.setProperty("--custom-tooltip-left", "0px");
        getBalance(currentChainIdHex, user.get("ethAddress"));
    }

    // Below address is on both BSC & ETH Scan - 
    // getBalance('0x1', '0x66A2C7B0d4Da7E699f6dD27f89b49AB15b0A51e8'); // eth chain
    // getBalance('0x38', '0x66A2C7B0d4Da7E699f6dD27f89b49AB15b0A51e8'); // bsc chain - this account has BUSD in it
}


function getUserWalletAddress() {
    let err = 'Wallet not connected';
    if (!isUserConnected())
        return err;

    return Moralis.User.current().get("ethAddress");
}

function isUserConnected() {
    let user = Moralis.User.current();
    return user ? true : false;
}

