async function getBalance(chainId, address) {
    const tokenList = document.getElementById('token-list');
    currentChainIdHex = await Moralis.getChainId();
    let currentUser = Moralis.User.current();
    let currentUserAddress = currentUser.get("ethAddress");
    const options = { chain: chainId || currentChainIdHex || '0x38', address: address || currentUserAddress || "0x9d51fd1a308c073f2f06a7181ad90c6d6ab5e9d7" };
    // console.log(`getBalance called with \n chaidId ${options.chain} and \n of address: ${options.address}`);
    // const options = { chain: 'eth', address: "0x9d51fd1a308c073f2f06a7181ad90c6d6ab5e9d7" } // test address from Etherscan
    // const options = { chain: 'bsc', address: "0x60913d3fE722bFE425247D7673D8bdbDA56D1e02" } // test address from BSC (bscscan)

    const nativeBal = await Moralis.Web3API.account.getNativeBalance(options);

    if (tokenList) tokenList.innerHTML = `<li class="pr-2 flex justify-between w-full md:w-1/2 mx-auto mt-2"><span class="w-1/2 text-center">${HEX_TO_CHAIN_DETAILS[currentChainIdHex].nativeToken}</span><span class="w-1/2 text-left">${web3.utils.fromWei(nativeBal.balance)}</span></li>`

    const balances = await Moralis.Web3API.account.getTokenBalances(options);

    if (balances.length) {
        balances.forEach(token => {
            // console.log(`not - ${token.symbol}`);
            if (token.symbol == 'USDC' || token.symbol == 'BUSD' || token.symbol == 'IBAT') {
                const balance = convertTokenBalance(token.balance, token.decimals);
                // const tokenFromWei = web3.utils.fromWei(token.balance)
                if (!tokenList) {
                    console.error('ln:226 - Token List Missing');
                } else {
                    tokenList.innerHTML += `<li class="pr-2 flex justify-between w-full md:w-1/2 mx-auto"><span class="w-1/2 text-center">${token.symbol}</span><span class="w-1/2 text-left">${balance}</span></li>`
                }
            }
        });
    } else {
        if (!tokenList) {
            console.error('ln:226 - Token List Missing');
        } else {
            tokenList.innerHTML += `<li class="w-full text-center mx-auto text-xs opacity-80 mt-2">no other tokens available</li>`
        }
    }
}


function toggleBtnVisibility(connected) {
    const allBtns = document.querySelectorAll('.wallet-options-btn ');
    if (connected) { //show
        allBtns.forEach(btn => {
            btn.classList.add('hidden');
        });
        logoutBtn && logoutBtn.classList.remove('hidden');
        showTokenBtn && showTokenBtn.classList.remove('hidden');
        // loginBtn && loginBtn.classList.remove('hidden');
    }
    else {
        if (!allBtns) {
            console.error(`ln:249-Invalid Arguments`);
        } else {
            allBtns.forEach(btn => {
                btn.classList.remove('hidden');
            });
        }
        // walletOptions && walletOptions.classList.remove('hidden');
        logoutBtn && logoutBtn.classList.add('hidden');
        showTokenBtn && showTokenBtn.classList.add('hidden');
    }
}

function toggleConnectStatus() {
    const statusFields = document.querySelectorAll('.connectivity-status');
    statusFields && statusFields.forEach(el => {
        if (isUserConnected()) {
            el.classList.remove('bg-red-400')
            el.style.background = "#4ade80";
            el.innerHTML = 'connected'
        } else {
            el.style.background = "#f87171";
            el.innerHTML = 'not connected'
        }
    });
}


function convertTokenBalance(balance, decimals) {
    let newBalance = balance == 0 ? 0 : balance / Math.pow(10, decimals);
    newBalance = newBalance < 0.001 ? 0 : newBalance;
    return newBalance;
}



