async function buyToken() {
    const value = +getBuyAmount();
    // console.log(`Ran buyToken() : value = ${value}`);

    await Moralis.enableWeb3();
    const errMsgContainer = document.getElementById('buy-token-error-con');
    const errMsg = document.getElementById('buy-token-error-msg');
    let msg = 'Error';

    if (!isUserConnected()) {
        console.log('User not Logged in - connect wallet');
        msg = 'Wallet not connected';
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }
    if (value <= 0) {
        msg = 'BNB Amount should be larged than 0.0';
        showErrMsg(errMsgContainer, errMsg, msg);
        return;
    }

    const FUNCTION_NAME = 'buyToken';
    const ABI = [{ "inputs": [], "name": "buyToken", "outputs": [{ "internalType": "address", "name": "", "type": "address" }], "stateMutability": "payable", "type": "function" }];
    let options = {
        contractAddress: CONTRACT_ADDRESS_TESTNET,
        functionName: FUNCTION_NAME,
        abi: ABI,
        msgValue: Moralis.Units.ETH(value)
    }
    let result;
    try {
        result = await Moralis.executeFunction(options);

        /**
         * Refresh the userInvestments field
         */
        setTimeout(refreshUsersInvestments, 1000);
        // console.log(`Result \n${result}`);

    } catch (error) {
        if (error.message) {
            console.log(error.message);
            msg = error.message;
            if (msg.includes('User denied transaction signature')) {
                msg = 'Transaction declined by User';
                showErrMsg(errMsgContainer, errMsg, msg);
                return;
            }
        }
        msg = error.data.message;
        console.log(msg);
        msg = 'Insufficient BNB in wallet';
        showErrMsg(errMsgContainer, errMsg, msg);
    }

}



